
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Spinner from 'vux/src/components/spinner/index.vue'

export default {
directives: {
TransferDom
},
components: {
Loading,
Spinner
},
data() {
return {
mobile:'',
passwd:'',
flag:true,
show1:false,
text1:'登陆中',
cont:60,
}
},
methods:{
handelout(){
this.flag = !this.flag;
let clock = window.setInterval(() => {
this.cont--
if (this.cont < 0) {
window.clearInterval(clock)
this.cont = 60;
this.flag = !this.flag;
}
},1000)
},
submit(){
this.show1 = true;
this.$router.push('/')














}
}
}



