import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate';
Vue.use(Vuex);
const store = new Vuex.Store({
  state:{
    /**
     * 登陆基本属性
     * logindata -> 用户信息
     * uuid -> 登陆标识
     * */
    uuid:'',
    loginname:'',
  },
  plugins: [createPersistedState()],
  mutations: {
    /**
     * 登陆设置方法
     * */
    loginname:(state,value) => state.loginname=value,
    uuid:(state,value) => {state.uuid = value},
  }
});
export default store;



