
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Spinner from 'vux/src/components/spinner/index.vue'

export default {
directives: {
TransferDom
},
components: {
Loading,
Spinner
},
data() {
return {
mobile:'',
passwd:'',
flag:true,
show1:false,
text1:'登陆中'
}
},
methods:{
handelout(){
this.flag = !this.flag
if(this.flag){
this.$refs.pass.setAttribute('type','password');
}else {
this.$refs.pass.setAttribute('type','text');
}
},
submit(){
this.show1 = true;
this.$router.push('/')














}
}
}



