import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
//登陆、注册
import login from '@/components/login/login'
import registered from '@/components/login/registered'
import textlogin from '@/components/login/textlogin'
//首页
import home from '@/components/index/home'

Vue.use(Router);

var router = new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index,
      redirect:'/home',
      children:[
        {
          path: '/login',
          name: '登陆',
          component: login
        },
        {
          path: '/registered',
          name: '注册',
          component: registered
        },
        {
          path: '/textlogin',
          name: '短信验证码登陆',
          component: textlogin
        },
        {
          path: '/home',
          name: '首页',
          component: home
        }
      ]
    },

  ]
})
