
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import { mapState, mapActions } from 'vuex'
export default {
components: {
ViewBox,
XHeader,
Drawer,
Tabbar,
TabbarItem
},
data() {
return {
sss:'',
titles:'',
drawerVisibility: false,
showModeValue:'push',
showPlacementValue:'right',
showMore:true,
direction:[],
tit:'',
names:'',
urlpath:false,
}
},
created(){
this.direction.push(this.$route.fullPath);
this.names= this.$route.name;
this.titles = this.$store.state.loginname.name;
if(this.$route.fullPath == '/login' || this.$route.fullPath == '/textlogin' || this.$route.fullPath == '/registered'){
this.urlpath = false;
this.isActive = false;
}else {
this.urlpath = true;
this.isActive = true;
}
},
methods:{
oitlin(){
this.showMore = true;
this. drawerVisibility= !this. drawerVisibility;
},
},
computed:{
forward(){
this.direction.forEach((e,i) => {
if(e === this.$route.fullPath){
this.direction.splice(i+1);
this.tit = 'vux-pop-out'
}else {
this.direction.push(this.$route.fullPath)
this.tit = 'vux-pop-in'
}
});
return this.tit;
}
},
watch:{
$route(to,from,next){
this.names = to.name;
if(to.path == '/login' || to.path == '/textlogin' || to.path == '/registered'){
this.urlpath = false;
this.isActive = false;
}else {
this.urlpath = true;
this.isActive = true;
}
}
}
}



